import React from 'react'
import {Col,Row} from "react-bootstrap"
const ProductDescribtion = () => {
    return (
        <div className='mediascreen'>
        <Row className="mt-2">
            <div className="cat-text">:</div>
        </Row>
        <Row>
            <Col md="8">
            <div className="cat-title d-inline">
                   128 
                <div className="cat-rate  mt-2">4.5</div>
            </div>
            </Col>
        </Row>
        <Row>
            <Col md="8" className="mt-4">
            <div className="cat-text d-inline"> :</div>
            <div className="barnd-text d-inline mx-1"></div>
            </Col>
        </Row>
        <Row>
            <Col md="8" className="mt-1 d-flex">
            <div className='color ms-2 border' style={{backgroundColor:"#E52C2c"}}></div>
            <div className='color ms-2 border' style={{backgroundColor:"white"}}></div>
            <div className='color ms-2 border' style={{backgroundColor:"black"}}></div>
                
            </Col>
        </Row>

        <Row className="mt-4">
            <div className="cat-text"> :</div>
        </Row>
        <Row className="mt-2">
            <Col md="10">
            <div className="product-description d-inline">
                         :-
                   128     NVMe     6   .
                   256     NVMe     6   .
                   512     NVMe     6   .
                          .
            </div>
            </Col>
        </Row>
        <Row className="mt-4">
            <Col md="12">
            <div className="product-price d-inline px-3 py-3 border"> 34000</div>
            <div  className="product-cart-add px-3 py-3 d-inline mx-3"> </div>
            </Col>
        </Row>
        </div>
    )
}

export default ProductDescribtion

