import React from 'react'
import { Link } from 'react-router-dom'
const Register = () => {
    return (
        <div>
        <div style={{minHeight:"650px"}} className='d-flex justify-content-center mt-5'>
        <div class="login-container ">
        <div class="login-header">
            <div>  </div>
        </div>
        <input type="text" class="login-input" placeholder=" " id="username"/>
        <input type="text" class="login-input" placeholder="" id="username"/>
        <input type="password" class="login-input" placeholder=" " id="password" maxlength="15" minlength="8"/>
        <button class="login-button" id="login-button"> </button>
        <h5 className='mt-3 text-center'>   
        <Link to="/login" style={{textDecoration:"none"}}>
        <span className='text-danger' style={{cursor:"pointer"}}> </span>
        </Link>
        </h5>
        </div>
    </div>
        </div>
    )
}

export default Register
